  /* Step 4: 33.1776px → 48.8281px */
  --step-4: clamp(2.0736rem, 1.8513rem + 1.1115vw, 3.0518rem);
  /* Step 5: 39.8131px → 61.0352px */
  --step-5: clamp(2.4883rem, 2.1869rem + 1.5072vw, 3.8147rem);
}

/*
---------------
Template
---------------
*/

body {
  font-size: var(--step-0);
  font-family: 'slabo_27pxregular', serif;

  background: #fff6f6;
  color: #111;
}

nav {
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 0.5rem 2rem;
}

.navbar-list {
  display: flex;
  align-items: center;
  gap: 2rem;
}

.navbar-list button {
  font-size: var(--step-1);
  padding: 0.5rem 2rem;

  border-radius: 40px;
  background: #e4989c;
  color: #fff;
  /* transparent border so that buttons don't move when hovered */
  border: 5px solid transparent;
}

.navbar-list button:hover,
.navbar-list button.active {
  border: 5px solid #47817e;
  cursor: pointer;
}

/*
---------------
Heading section for all three tabs
---------------
*/

.heading-container {
  display: flex;
  justify-content: center;
  align-items: center;
  height: 650px;
}

.heading-container h1 {
  font-size: var(--step-5);
  font-weight: normal;
  padding: 1rem 3rem;

  background-color: rgba(255, 246, 246, 0.6);
  border-radius: 40px;
}

/*
---------------
Footer for all three tabs
---------------
*/

footer {
  text-align: center;
  padding: 1rem 2rem;

  background: #f1bad1;
}

footer a {
  font-size: var(--step--1);
  padding: 0 1rem;

  color: #fff;
}

footer a:not(:last-child) {
  border-right: 2px solid #fff6f6;
}

footer a:hover {
  text-decoration: underline;
}

/*
---------------
Home
---------------
*/

.home-heading {
  background-image: url(${___CSS_LOADER_URL_REPLACEMENT_1___});
  background-size: cover;
  background-position: bottom;
}

.welcome-container {
  display: flex;
  justify-content: center;
  align-items: center;
  padding: 10rem 2rem;
  gap: 5rem;
}

.welcome-container h2 {
  font-size: var(--step-3);
  text-align: center;
}

.welcome-container p {
  max-width: 60ch;
  margin-top: 1rem;
  text-align: center;
}

.mimosa-img {
  width: 300px;

  border-radius: 40px;
}

/*
---------------
Menu
---------------
*/

.menu-heading {
  background-image: url(${___CSS_LOADER_URL_REPLACEMENT_2___});
  background-size: cover;
}

.menu-container {
  display: flex;
  justify-content: center;
  align-items: center;
  padding: 10rem 2rem;
}

.menu {
  padding: 4rem 2rem;
  text-align: center;

  border-radius: 40px;
  background: #47817e;
  color: #fff;
}

.menu h2 {
  font-size: var(--step-2);
}

.menu-price {
  font-size: var(--step-1);
}

.menu-price:not(:last-child) {
  margin-bottom: 3rem;
}

/*
---------------
Contact
---------------
*/

.contact-heading {
  background-image: url(${___CSS_LOADER_URL_REPLACEMENT_3___});
  background-size: cover;
  background-position: center;
}

.contact-container {
  display: flex;
  justify-content: center;
  align-items: center;
  padding: 10rem 2rem;
}

.contact {
  padding: 4rem 2rem;

  border-radius: 40px;
  background: #b0eaea;
  color: #47817e;
}

.contact p {
  font-size: var(--step-2);
}

.contact-item-container {
  display: flex;
  align-items: center;
  gap: 1rem;
  margin: 1rem 0;
}

.contact-item-container:last-of-type {
  padding-bottom: 3rem;
  margin-bottom: 3rem;

  border-bottom: 2px solid #fff;
}

.contact-icon {
  width: 70px;
}

.weekend-hours {
  margin-top: 1rem;
}

/*
---------------
Media Queries
---------------
*/

/* navbar */
@media (max-width: 700px) {
  nav,
  .navbar-list {
    flex-direction: column;
  }

  nav {
    padding-bottom: 2rem;
  }

  footer {
    display: flex;
    flex-direction: column;
  }
  footer a:not(:last-child) {
    border-right: none;
  }

  /* home */
  .welcome-container {
    flex-direction: column;
  }
}
